@mixin responsive($breakpoint) {
	@if $breakpoint == xs-1 {
		@media (min-width: 375px) {
			@content;
		}
	} @else if $breakpoint == xs-2 {
		@media (min-width: 390px) and (min-height: 844px) {
			@content;
		}
	} @else if $breakpoint == md-1 {
		@media (min-width: 412px) {
			@content;
		}
	} @else if $breakpoint == md-2 {
		@media (min-width: 576px) {
			@content;
		}
	} @else if $breakpoint == md-3 {
		@media (min-width: 768px) {
			@content;
		}
	} @else if $breakpoint == lg {
		@media (min-width: 992px) {
			@content;
		}
	} @else if $breakpoint == xl {
		@media (min-width: 1200px) {
			@content;
		}
	} @else if $breakpoint == xxl {
		@media (min-width: 1400px) {
			@content;
		}
	} @else if $breakpoint == high-sm-1 {
		@media (min-height: 667px) and (max-height: 705px) {
			@content;
		}
	} @else if $breakpoint == high-sm-2 {
		@media (min-height: 740px) {
			@content;
		}
	} @else if $breakpoint == high-sm-3 {
		@media (min-height: 800px) {
			@content;
		}
	} @else if $breakpoint == high-md-1 {
		@media (min-height: 840px) {
			@content;
		}
	} @else if $breakpoint == high-md-2 {
		@media (min-height: 860px) {
			@content;
		}
	} @else if $breakpoint == high-lg {
		@media (min-height: 915px) {
			@content;
		}
	}
}
